}

.new-window-tip {
  margin-bottom: 16px;
  border-radius: 6px;
}

.new-window-tip p {
  margin: 4px 0;
  font-size: 12px;
  line-height: 1.4;
}

.sidebar-tip {
  margin-bottom: 16px;
  border-radius: 6px;
}

.sidebar-tip p {
  margin: 4px 0;
  font-size: 12px;
  line-height: 1.4;
}

.data-section h4 {
  margin: 0 0 12px 0;
  color: #666;
  font-size: 14px;
}

.empty-state {
  text-align: center;
  padding: 40px 20px;
  color: #999;
  background: white;
  border-radius: 4px;
  border: 1px dashed #ddd;
}

.request-list {
  background: white;
  border-radius: 4px;
  border: 1px solid #e0e0e0;
  overflow: hidden;
