16px;\r\n  transition: background-color 0.2s;\n}\n.request-item[data-v-42c7142d]:last-child {\r\n  border-bottom: none;\n}\n.request-item[data-v-42c7142d]:hover {\r\n  background-color: #fafafa;\n}\n.request-item.parsing[data-v-42c7142d] {\r\n  background-color: #f0f9ff;\r\n  box-shadow: 0 2px 8px rgba(24, 144, 255, 0.1);\r\n  animation: pulse-42c7142d 2s ease-in-out infinite;\n}\n@keyframes pulse-42c7142d {\n0% {\r\n    box-shadow: 0 2px 8px rgba(24, 144, 255, 0.1);\n}\n50% {\r\n    box-shadow: 0 4px 16px rgba(24, 144, 255, 0.2);\n}\n100% {\r\n    box-shadow: 0 2px 8px rgba(24, 144, 255, 0.1);\n}\n}\n.request-info[data-v-42c7142d] {\r\n  margin-bottom: 8px;\n}\n.request-url[data-v-42c7142d] {\r\n  font-family: 'Monaco', 'Menlo', 'Ubuntu Mono', monospace;\r\n  font-size: 12px;\r\n  color: #333;\r\n  word-break: break-all;\r\n  margin-bottom: 4px;\n}\n.request-meta[data-v-42c7142d] {\r\n  display: flex;\r\n  gap: 12px;\r\n  font-size: 11px;\n}\n.method[data-v-42c7142d] {\r\n  background: #1890ff;\r\n  color: white;\r\n  padding: 2px 6px;\r\n  border-radius: 2px;\r\n  font-weight: bold;\n}\n.status[data-v-42c7142d] {\r\n  padding: 2px 6px;\r\n  border-radius: 2px;\r\n  font-weight: bold;\n}\n.status.pending[data-v-42c7142d] {\r\n  background: #faad14;\r\n  color: white;\n}\n.status.completed[data-v-42c7142d] {\r\n  background: #52c41a;\r\n  color: white;\n}\n.status.parsing[data-v-42c7142d] {\r\n  background: #1890ff;\r\n  color: white;\n}\n.status.parsing .el-icon-loading[data-v-42c7142d] {\r\n  animation: rotating-42c7142d 2s linear infinite;\n}\n@keyframes rotating-42c7142d {\n0% {\r\n    transform: rotate(0deg);\n}\n100% {\r\n    transform: rotate(360deg);\n}\n}\n.status.failed[data-v-42c7142d] {\r\n  background: #ff4d4f;\r\n  color: white;\n}\n.timestamp[data-v-42c7142d] {\r\n  color: #999;\n}\n.response-data[data-v-42c7142d] {\r\n  margin-top: 8px;\r\n  padding-top: 8px;\r\n  border-top: 1px solid #f0f0f0;\n}\n.response-content[data-v-42c7142d] {\r\n  margin-top