late(-50%, -50%) scale(1.2);\r\n    box-shadow: 0 0 0 5px rgba(250, 173, 20, 0.2);\r\n    opacity: 0.7;\r\n  }\r\n\r\n  100% {\r\n    transform: translate(-50%, -50%) scale(1);\r\n    box-shadow: 0 0 0 0 rgba(250, 173, 20, 0.7);\r\n    opacity: 1;\r\n  }\r\n}\r\n\r\n.status-text {\r\n  font-size: 12px;\r\n  color: #666;\r\n  font-weight: 500;\r\n}\r\n\r\n.mode-indicator {\r\n  font-size: 10px;\r\n  padding: 2px 6px;\r\n  border-radius: 3px;\r\n  font-weight: bold;\r\n  margin-left: 8px;\r\n}\r\n\r\n.mode-indicator.sidepanel {\r\n  background-color: #1890ff;\r\n  color: white;\r\n}\r\n\r\n.mode-indicator.new-window {\r\n  background-color: #52c41a;\r\n  color: white;\r\n}\r\n\r\n.new-window-tip {\r\n  margin-bottom: 16px;\r\n  border-radius: 6px;\r\n}\r\n\r\n.new-window-tip p {\r\n  margin: 4px 0;\r\n  font-size: 12px;\r\n  line-height: 1.4;\r\n}\r\n\r\n.sidebar-tip {\r\n  margin-bottom: 16px;\r\n  border-radius: 6px;\r\n}\r\n\r\n.sidebar-tip p {\r\n  margin: 4px 0;\r\n  font-size: 12px;\r\n  line-height: 1.4;\r\n}\r\n\r\n.data-section h4 {\r\n  margin: 0 0 12px 0;\r\n  color: #666;\r\n  font-size: 14px;\r\n}\r\n\r\n.empty-state {\r\n  text-align: center;\r\n  padding: 40px 20px;\r\n  color: #999;\r\n  background: white;\r\n  border-radius: 4px;\r\n  border: 1px dashed #ddd;\r\n}\r\n\r\n.request-list {\r\n  background: white;\r\n  border-radius: 4px;\r\n  border: 1px solid #e0e0e0;\r\n  overflow: hidden;\r\n}\r\n\r\n.request-item {\r\n  border-bottom: 1px solid #f0f0f0;\r\n  padding: 12px 16px;\r\n  transition: background-color 0.2s;\r\n}\r\n\r\n.request-item:last-child {\r\n  border-bottom: none;\r\n}\r\n\r\n.request-item:hover {\r\n  background-color: #fafafa;\r\n}\r\n\r\n.request-item.parsing {\r\n  background-color: #f0f9ff;\r\n  box-shadow: 0 2px 8px rgba(24, 144, 255, 0.1);\r\n  animation: pulse 2s ease-in-out infinite;\r\n}\r\n\r\n@keyframes pulse {\r\n  0% {\r\n    box-shadow: 0 2px 8px rgba(24, 144, 255, 0.1);\r\n  }\r\n\r\n  50% {\r\n    box-shadow: 0 4px 16px rgba(24, 144, 255, 0.2);\r\n  }\r\n\r\n  100% {\r\n    box-shadow: 0 2px 8px rgba(24, 144, 255, 0.1);\r\n  }\r\n}\r\n\r\n.request-info {\r\n  margin-bottom: 8px;\r\n}\r\n\r\n.request-url {\r\n  font-family: 'Monaco', 'Menlo', 'Ubuntu Mono', monospace;\r\n  font-size: 12px;\r\n  color: #333;\r\n  word-break: break-all;\r\n  margin-bottom: 4px;\r\n}\r\n\r\n.request-meta {\r\n  display: flex;\r\n  gap: 12px;\r\n  font-size: 11px;\r\n}\r\n\r\n.method {\r\n  background: #1890ff;\r\n  color: white;\r\n  padding: 2px 6px;\r\n  border-radius: 2px;\r\n  font-weight: bold;\r\n}\r\n\r\n.status {\r\n  padding: 2px 6px;\r\n  border-radius: 2px;\r\n  font-weight: bold;\r\n}\r\n\r\n.status.pending {\r\n  background: #faad14;\r\n  color: white;\r\n}\r\n\r\n.status.completed {\r\n  background: #52c41a;\r\n  color: white;\r\n}\r\n\r\n.status.parsing {\r\n  background: #1890ff;\r\n  color: white;\r\n}\r\n\r\n.status.parsing .el-icon-loading {\r\n  animation: rotating 2s linear infinite;\r\n}\r\n\r\n@keyframes rotating {\r\n  0% {\r\n    transform: rotate(0deg);\r\n  }\r\n\r\n  100% {\r\n    transform: rotate(360deg);\r\n  }\r\n}\r\n\r\n.status.failed {\r\n  background: #ff4d4f;\r\n  color: white;\r\n}\r\n\r\n.timestamp {\r\n  color: #999;\r\n}\r\n\r\n.response-data {\r\n  margin-top: 8px;\r\n  padding-top: 8px;\r\n  border-top: 1px solid #f0f0f0;\r\n}\r\n\r\n.response-content {\r\n  margin-top: 8px;\r\n  background: #f8f8f8;\r\n  border-radius: 4px;\r\n  padding: 8px;\r\n  max-height: 300px;\r\n  overflow-y: auto;\r\n}\r\n\r\n.response-content pre {\r\n  margin: 0;\r\n  font-family: 'Monaco', 'Menlo', 'Ubuntu Mono', monospace;\r\n  font-size: 11px;\r\n  line-height: 1.4;\r\n  color: #333;\r\n  white-space: pre-wrap;\r\n  word-break: break-word;\r\n}\r\n\r\n.role-card /deep/.el-card__body {\r\n  padding: 8px;\r\n}\r\n\r\n/*  */\r\n.role-card.empty-role {\r\n  opacity: 0.6;\r\n  filter: grayscale(0.8);\r\n  background-color: #f5f5f5;\r\n  border: 1px solid #d9d9d9;\r\n  transition: 