<template>
  <el-dialog
    :title="isRegister ? '' : ''"
    :visible.sync="visible"
    width="400px"
    :close-on-click-modal="true"
    :close-on-press-escape="true"
    :show-close="true"
    :modal="true"
    class="login-modal"
  >
    <!--  -->
    <el-form 
      v-if="!isRegister"
      :model="loginForm" 
      :rules="rules" 
      ref="loginForm" 
      label-width="80px"
    >
      <el-form-item label="" prop="username">
        <el-input 
          v-model="loginForm.username" 
          placeholder="" 
          autocomplete="off"
        ></el-input>
      </el-form-item>
      <el-form-item label="" prop="password">
        <el-input
          v-model="loginForm.password"
          type="password"
          placeholder=""
          autocomplete="off"
          @keyup.enter.native="handleLogin"
        ></el-input>
      </el-form-item>
    </el-form>
    
    <!--  -->
    <el-form 
      v-else
      :model="registerForm" 
      :rules="registerRules" 
      ref="registerForm" 
      label-width="80px"
    >
      <el-form-item label="" prop="username">
        <el-input 
          v-model="registerForm.username" 
          placeholder="" 
          autocomplete="off"
        ></el-input>
      </el-form-item>
      <el-form-item label="" prop="password">
        <el-input
          v-model="registerForm.password"
          type="password"
          placeholder=""
          autocomplete="off"
          @keyup.enter.native="handleRegister"
        ></el-input>
      </el-form-item>
      <el-form-item label="" prop="confirmPassword">
        <el-input
          v-model="registerForm.confirmPassword"
          type="password"
          placeholder=""
          autocomplete="off"
          @keyup.enter.native="handleRegister"
        ></el-input>
      </el-form-item>
    </el-form>
    
    <div slot="footer" class="dialog-footer">
      <el-button @click="toggleMode">{{ isRegister ? '' : '' }}</el-button>
      <el-button 
        v-if="!isRegister"
        type="primary" 
        @click="handleLogin" 
        :loading="loading"
      >
        
      </el-button>
      <el-button 
        v-else
        type="primary" 
        @click="handleRegister" 
        :loading="loading"
      >
        
      </el-button>
    </div>
  </el-dialog>
</template>

<script>
import { api } from '@/utils/request'

export default {
  name: 'LoginModal',
  props: {
    value: {
      type: Boolean,
      default: false
    }
  },
  data() {
    // 
    const validateConfirmPassword = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''))
      } else if (value !== this.registerForm.password) {
        callback(new Error(''))
      } else {
        callback()
      }
    }
    
    return {
      visible: this.value,
      loading: false,
      isRegister: false, // 
      loginForm: {
        username: '',
        password: ''
      },
      registerForm: {
        username: '',
        password: '',
        confirmPassword: ''
      },
      rules: {
        username: [
          { required: true, message: '', trigger: 'blur' },
          { pattern: /^1[3-9]\d{9}$/, message: '', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '', trigger: 'blur' },
          { min: 6, message: '6', trigger: 'blur' }
        ]
      },
      registerRules: {
        username: [
          { required: true, message: '', trigger: 'blur' },
          { pattern: /^1[3-9]\d{9}$/, message: '', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '', trigger: 'blur' },
          { min: 6, message: '6', trigger: 'blur' }
        ],
        confirmPassword: [
          { required: true, message: '', trigger: 'blur' },
          { validator: validateConfirmPassword, trigger: 'blur' }
        ]
      }
    }
  },
  watch: {
    value(newVal) {
      this.visible = newVal
    },
    visible(newVal) {
      this.$emit('input', newVal)
    }
  },
  methods: {
    async handleLogin() {
      this.$refs.loginForm.validate(async (valid) => {
        if (!valid) return
        
        this.loading = true
        try {
          const response = await api.post('/auth/login', {
            username: this.loginForm.username,
            password: this.loginForm.password
          })
          
          if (response.code === 200) {
            // 
            if (response.message && response.message.includes('')) {
              this.$message.info(response.message)
            } else if (response.data && response.data.token) {
              // tokenchrome.storage
              await this.saveToken(response.data.token, response.data.user)
              
              this.$message.success('')
              this.$emit('login-success', response.data)
              this.visible = false
            } else {
              // 
              this.$message.info(response.message || '')
            }
          } else {
            this.$message.error(response.message || '')
          }
        } catch (error) {
          console.error(':', error)
          this.$message.error('')
        } finally {
          this.loading = false
        }
      })
    },
    
    toggleMode() {
      this.isRegister = !this.isRegister
      //  DOM 
      this.$nextTick(() => {
        if (this.$refs.loginForm) {
          this.$refs.loginForm.resetFields()
        }
        if (this.$refs.registerForm) {
          this.$refs.registerForm.resetFields()
        }
      })
    },
    
    async handleRegister() {
      // 
      if (!this.$refs.registerForm) {
        console.error('')
        this.$message.error('')
        return
      }
      
      this.$refs.registerForm.validate(async (valid) => {
        if (!valid) {
          console.log('')
          this.$message.warning('')
          return
        }
        
        console.log(':', this.registerForm.username)
        this.loading = true
        try {
          const response = await api.post('/auth/register', {
            username: this.registerForm.username,
            password: this.registerForm.password
          })
          
          console.log(':', response)
          
          if (response.code === 200) {
            this.$message.success(response.message || '')
            // 
            const username = this.registerForm.username
            // 
            this.isRegister = false
            //  DOM 
            this.$nextTick(() => {
              this.loginForm.username = username
              if (this.$refs.registerForm) {
                this.$refs.registerForm.resetFields()
              }
            })
          } else {
            this.$message.error(response.message || '')
          }
        } catch (error) {
          console.error(':', error)
          // 
          const errorMessage = error.response?.data?.message || error.message || ''
          this.$message.error(errorMessage)
        } finally {
          this.loading = false
        }
      })
    },
    
    async saveToken(token, user) {
      return new Promise((resolve) => {
        chrome.storage.local.set({
          auth_token: token,
          user_info: user
        }, () => {
          resolve()
        })
      })
    }
  }
}
</script>

<style scoped>
.login-modal {
  z-index: 10000;
}

.dialog-footer {
  text-align: right;
}
</style>

