import axios from 'axios'
import { Notification } from 'element-ui'

// Chrome
const isChromeExtension = typeof chrome !== 'undefined' && chrome.runtime && chrome.runtime.id

// baseURL
const baseURL = isChromeExtension ? 'http://localhost:5000/api/v1' : '/api/v1'
// const baseURL = isChromeExtension ? 'http://xyq.lingtong.xyz/api/v1' : '/api/v1'

//  fingerprint cookie
let cachedFingerprint = ''
let fingerprintCacheTime = 0
const FINGERPRINT_CACHE_DURATION = 5 * 60 * 1000 // 5

//  auth token
let cachedAuthToken = null
let authTokenCacheTime = 0
const AUTH_TOKEN_CACHE_DURATION = 5 * 60 * 1000 // 5

// chrome.storageauth token
const getAuthTokenFromStorage = async () => {
  if (!isChromeExtension) {
    return null
  }
  
  return new Promise((resolve) => {
    chrome.storage.local.get(['auth_token'], (result) => {
      if (result.auth_token) {
        cachedAuthToken = result.auth_token
        authTokenCacheTime = Date.now()
        resolve(result.auth_token)
      } else {
        resolve(null)
      }
    })
  })
}

// auth token
const getAuthToken = () => {
  // 
  const now = Date.now()
  if (now - authTokenCacheTime > AUTH_TOKEN_CACHE_DURATION) {
    getAuthTokenFromStorage().catch(err => {
      console.error(' auth token :', err)
    })
  }
  
  return cachedAuthToken
}

// auth token
export const initAuthToken = async () => {
  if (isChromeExtension) {
    cachedAuthToken = await getAuthTokenFromStorage()
  }
}

// auth token
export const clearAuthToken = () => {
  cachedAuthToken = null
  authTokenCacheTime = 0
  if (isChromeExtension) {
    chrome.storage.local.remove(['auth_token', 'user_info'])
  }
}

//  fingerprint cookie
const getFingerprintCookieFromTarget = async () => {
  if (!isChromeExtension) {
    return ''
  }

  try {
    // 
    const [activeTab] = await chrome.tabs.query({ active: true, currentWindow: true })
    
    if (!activeTab || !activeTab.url || !activeTab.url.includes('cbg.163.com')) {
      return ''
    }

    //  Chrome Debugger API  cookies
    const result = await chrome.debugger.sendCommand(
      { tabId: activeTab.id },
      'Network.getAllCookies'
    )

    const cookies = Array.isArray(result?.cookies) ? result.cookies : []
    
    //  domain  xyq.cbg.163.com  name  fingerprint  cookie
    const fingerprintCookie = cookies.find(
      cookie => cookie.name === 'fingerprint' && 
                cookie.domain && 
                cookie.domain.includes('xyq.cbg.163.com')
    )

    if (fingerprintCookie) {
      cachedFingerprint = fingerprintCookie.value || ''
      fingerprintCacheTime = Date.now()
      return cachedFingerprint
    }

    return ''
  } catch (error) {
    console.error(' fingerprint cookie :', error)
    return ''
  }
}

//  fingerprint cookie
const getFingerprintCookie = () => {
  // 
  const now = Date.now()
  if (now - fingerprintCacheTime > FINGERPRINT_CACHE_DURATION) {
    getFingerprintCookieFromTarget().catch(err => {
      console.error(' fingerprint cookie :', err)
    })
  }
  
  return cachedFingerprint
}

//  fingerprint cookie
export const initFingerprintCookie = async () => {
  if (isChromeExtension) {
    await getFingerprintCookieFromTarget()
  }
}

// axios
const request = axios.create({
  baseURL: baseURL, // API
  headers: {
    'Content-Type': 'application/json'
  }
})

// 
request.interceptors.request.use(
  async config => {
    // 
    console.log(':', config.method?.toUpperCase(), config.url)
    
    // fingerprint
    const fingerprint = getFingerprintCookie()||'admin'
    if (fingerprint) {
      config.headers['X-Fingerprint'] = fingerprint
    }
    
    // auth token
    if (!config.url.includes('/auth/login') && !config.url.includes('/auth/register')) {
      let token = null
      
      // Chromechrome.storagetoken
      if (isChromeExtension) {
        token = getAuthToken()
      } else {
        // WeblocalStoragetoken
        token = localStorage.getItem('auth_token')
      }
      
      if (token) {
        config.headers['Authorization'] = `Bearer ${token}`
      }
    }
    
    return config
  },
  error => {
    // 
    console.error(':', error)
    return Promise.reject(error)
  }
)

// 
request.interceptors.response.use(
  response => {
    // 
    const { data } = response
    
    return data
  },
  error => {
    // 
    console.error(':', error)
    
    let message = ''
    let responseData = null
    
    if (error.response) {
      // 
      const { status, data } = error.response
      message = data.message || ` (${status})`
      
      // datadata
      if (data.data !== undefined) {
        responseData = data.data
      } else {
        responseData = data
      }
      
      // 401token
      if (status === 401) {
        if (isChromeExtension) {
          clearAuthToken()
          // 
          if (typeof window !== 'undefined' && window.dispatchEvent) {
            window.dispatchEvent(new CustomEvent('auth-required'))
          }
        } else {
          // WeblocalStorage
          localStorage.removeItem('auth_token')
          localStorage.removeItem('user_info')
          // Header
          if (typeof window !== 'undefined' && window.dispatchEvent) {
            window.dispatchEvent(new CustomEvent('auth-required'))
          }
        }
      }
      
      console.log(':', responseData)
    } else if (error.request) {
      // 
      message = ''
    } else {
      // 
      message = error.message || ''
    }
    
    Notification.error({
      title: '',
      message: message
    })
    
    return {
      code: error.response?.status || 500,
      data: responseData,  // datanull
      message: message,
      timestamp: Date.now()
    }
  }
)

// 
export const api = {
  // GET
  get(url, params = {}) {
    return request.get(url, { params })
  },
  
  // POST
  post(url, data = {}) {
    return request.post(url, data)
  },
  
  // PUT
  put(url, data = {}) {
    return request.put(url, data)
  },
  
  // DELETE
  delete(url, params = {}) {
    return request.delete(url, { params })
  },
  
  // 
  download(url, params = {}, filename) {
    return request.get(url, { 
      params,
      responseType: 'blob'
    }).then(response => {
      if (response.code === 200) {
        // 
        const blob = new Blob([response.data])
        const downloadUrl = window.URL.createObjectURL(blob)
        const link = document.createElement('a')
        link.href = downloadUrl
        link.download = filename || 'download'
        document.body.appendChild(link)
        link.click()
        document.body.removeChild(link)
        window.URL.revokeObjectURL(downloadUrl)
      }
      return response
    })
  }
}

export default request 