___CSS_LOADER_EXPORT___.push([module.id, "\n.login-modal[data-v-16190305] {\r\n  z-index: 10000;\n}\n.dialog-footer[data-v-16190305] {\r\n  text-align: right;\n}\r\n", "",{"version":3,"sources":["webpack://./src/chrome-extensions/LoginModal.vue"],"names":[],"mappings":";AA2RA;EACA,cAAA;AACA;AAEA;EACA,iBAAA;AACA","sourcesContent":["<template>\r\n  <el-dialog\r\n    :title=\"isRegister ? '' : ''\"\r\n    :visible.sync=\"visible\"\r\n    width=\"400px\"\r\n    :close-on-click-modal=\"true\"\r\n    :close-on-press-escape=\"true\"\r\n    :show-close=\"true\"\r\n    :modal=\"true\"\r\n    class=\"login-modal\"\r\n  >\r\n    <!--  -->\r\n    <el-form \r\n      v-if=\"!isRegister\"\r\n      :model=\"loginForm\" \r\n      :rules=\"rules\" \r\n      ref=\"loginForm\" \r\n      label-width=\"80px\"\r\n    >\r\n      <el-form-item label=\"\" prop=\"username\">\r\n        <el-input \r\n          v-model=\"loginForm.username\" \r\n          placeholder=\"\" \r\n          autocomplete=\"off\"\r\n        ></el-input>\r\n      </el-form-item>\r\n      <el-form-item label=\"\" prop=\"password\">\r\n        <el-input\r\n          v-model=\"loginForm.password\"\r\n          type=\"password\"\r\n          placeholder=\"\"\r\n          autocomplete=\"off\"\r\n          @keyup.enter.native=\"handleLogin\"\r\n        ></el-input>\r\n      </el-form-item>\r\n    </el-form>\r\n    \r\n    <!--  -->\r\n    <el-form \r\n      v-else\r\n      :model=\"registerForm\" \r\n      :rules=\"registerRules\" \r\n      ref=\"registerForm\" \r\n      label-width=\"80px\"\r\n    >\r\n      <el-form-item label=\"\" prop=\"username\">\r\n        <el-input \r\n          v-model=\"registerForm.username\" \r\n          placeholder=\"\" \r\n          autocomplete=\"off\"\r\n        ></el-input>\r\n      </el-form-item>\r\n      <el-form-item label=\"\" prop=\"password\">\r\n        <el-input\r\n          v-model=\"registerForm.password\"\r\n          type=\"password\"\r\n          placeholder=\"\"\r\n          autocomplete=\"off\"\r\n          @keyup.enter.native=\"handleRegister\"\r\n        ></el-input>\r\n      </el-form-item>\r\n      <el-form-item label=\"\" prop=\"confirmPassword\">\r\n        <el-input\r\n          v-model=\"registerForm.confirmPassword\"\r\n          type=\"password\"\r\n          placeholder=\"\"\r\n          autocomplete=\"off\"\r\n          @keyup.enter.native=\"handleRegister\"\r\n        ></el-input>\r\n      </el-form-item>\r\n    </el-form>\r\n    \r\n    <div slot=\"footer\" class=\"dialog-footer\">\r\n      <el-button @click=\"toggleMode\">{{ isRegister ? '' : '' }}</el-button>\r\n      <el-button \r\n        v-if=\"!isRegister\"\r\n        type=\"primary\" \r\n        @click=\"handleLogin\" \r\n        :loading=\"loading\"\r\n      >\r\n        \r\n      </el-button>\r\n      <el-button \r\n        v-else\r\n        type=\"primary\" \r\n        @click=\"handleRegister\" \r\n        :loading=\"loading\"\r\n      >\r\n        \r\n      </el-button>\r\n    </div>\r\n  </el-dialog>\r\n</template>\r\n\r\n<script>\r\nimport { api } from '@/utils/request'\r\n\r\nexport default {\r\n  name: 'LoginModal',\r\n  props: {\r\n    value: {\r\n      type: Boolean,\r\n      default: false\r\n    }\r\n  },\r\n  data() {\r\n    // \r\n    const validateConfirmPassword = (rule, value, callback) => {\r\n      if (value === '') {\r\n        callback(new Error(''))\r\n      } else if (value !== this.registerForm.password) {\r\n        callback(new Error(''))\r\n      } else {\r\n        callback()\r\n      }\r\n    }\r\n    \r\n    return {\r\n      visible: this.value,\r\n      loading: false,\r\n      isRegister: false, // \r\n      loginForm: {\r\n        username: '',\r\n        password: ''\r\n      },\r\n      registerForm: {\r\n        username: '',\r\n        password: '',\r\n        confirmPassword: ''\r\n      },\r\n      rules: {\r\n        username: [\r\n          { required: true, message: '', trigger: 'blur' },\r\n          { pattern: /^1[3-9]\\d{9}$/, message: '', trigger: 'blur' }\r\n        ],\r\n        password: [\r\n          { required: true, message: '', trigger: 'blur' },\r\n          { min: 6, message: '6', trigger: 'blur' }\r\n        ]\r\n      },\r\n      registerRules: {\r\n        username: [\r\n          { required: true, message: '', trigger: 'blur' },\r\n          { pattern: /^1[3-9]\\d{9}$/, message: '', trigger: 'blur' }\r\n        ],\r\n        password: [\r\n          { required: true, message: '', trigger: 'blur' },\r\n          { min: 6, message: '6', trigger: 'blur' }\r\n        ],\r\n        confirmPassword: [\r\n          { required: true, message: '', trigger: 'blur' },\r\n          { validator: validateConfirmPassword, trigger: 'blur' }\r\n        ]\r\n      }\r\n    }\r\n  },\r\n  watch: {\r\n    value(newVal) {\r\n      this.visible = newVal\r\n    },\r\n    visible(newVal) {\r\n      this.$emit('input', newVal)\r\n    }\r\n  },\r\n  methods: {\r\n    async handleLogin() {\r\n      this.$refs.loginForm.validate(async (valid) => {\r\n        if (!valid) return\r\n        \r\n        this.loading = true\r\n        try {\r\n          const response = await api.post('/auth/login', {\r\n            username: this.loginForm.username,\r\n            password: this.loginForm.password\r\n          })\r\n          \r\n          if (response.code === 200) {\r\n            // \r\n            if (response.message && response.message.includes('')) {\r\n              this.$message.info(response.message)\r\n            } else if (response.data && response.data.token) {\r\n              // tokenchrome.storage\r\n              await this.saveToken(response.data.token, response.data.user)\r\n              \r\n              this.$message.success('')\r\n              this.$emit('login-success', response.data)\r\n              this.visible = false\r\n            } else {\r\n              // \r\n              this.$message.info(response.message || '')\r\n            }\r\n          } else {\r\n            this.$message.error(response.message || '')\r\n          }\r\n        } catch (error) {\r\n          console.error(':', error)\r\n          this.$message.error('')\r\n        } finally {\r\n          this.loading = false\r\n        }\r\n      })\r\n    },\r\n    \r\n    toggleMode() {\r\n      this.isRegister = !this.isRegister\r\n      //  DOM \r\n      this.$nextTick(() => {\r\n        if (this.$refs.loginForm) {\r\n          this.$refs.loginForm.resetFields()\r\n        }\r\n        if (this.$refs.registerForm) {\r\n          this.$refs.registerForm.resetFields()\r\n        }\r\n      })\r\n    },\r\n    \r\n    async handleRegister() {\r\n      // \r\n      if (!this.$refs.registerForm) {\r\n        console.error('')\r\n        this.$message.error('')\r\n        return\r\n      }\r\n      \r\n      this.$refs.registerForm.validate(async (valid) => {\r\n        if (!valid) {\r\n          console.log('')\r\n          this.$message.warning('')\r\n          return\r\n        }\r\n        \r\n        console.log(':', this.registerForm.username)\r\n        this.loading = true\r\n        try {\r\n          const response = await api.post('/auth/register', {\r\n            username: this.registerForm.username,\r\n            password: this.registerForm.password\r\n          })\r\n          \r\n          console.log(':', response)\r\n          \r\n          if (response.code === 200) {\r\n            this.$message.success(response.message || '')\r\n            // \r\n            const username = this.registerForm.username\r\n            // \r\n            this.isRegister = false\r\n            //  DOM \r\n            this.$nextTick(() => {\r\n              this.loginForm.username = username\r\n              if (this.$refs.registerForm) {\r\n                this.$refs.registerForm.resetFields()\r\n              }\r\n            })\r\n          } else {\r\n            this.$message.error(response.message || '')\r\n          }\r\n        } catch (error) {\r\n          console.error(':', error)\r\n          // \r\n          const errorMessage = error.response?.data?.message || error.message || ''\r\n          this.$message.error(errorMessage)\r\n        } finally {\r\n          this.loading = false\r\n        }\r\n      })\r\n    },\r\n    \r\n    async saveToken(token, user) {\r\n      return new Promise((resolve) => {\r\n        chrome.storage.local.set({\r\n          auth_token: token,\r\n          user_info: user\r\n        }, () => {\r\n          resolve()\r\n        })\r\n      })\r\n    }\r\n  }\r\n}\r\n</script>\r\n\r\n<style scoped>\r\n.login-modal {\r\n  z-index: 10000;\r\n}\r\n\r\n.dialog-footer {\r\n  text-align: right;\r\n}\r\n</style>\r\n\r\n"],"sourceRoot":""}]);
