/**
 * Mixin
 * 
 */
export const authMixin = {
  methods: {
    /**
     * 
     * @returns {Promise<boolean>} 
     */
    async checkAuthStatus() {
      //  userInfo
      if (this.userInfo !== null && this.userInfo !== undefined) {
        return true
      }
      
      //  userInfo chrome.storage  token
      if (typeof chrome !== 'undefined' && chrome.storage) {
        try {
          const result = await new Promise((resolve) => {
            chrome.storage.local.get(['auth_token', 'user_info'], (result) => {
              resolve(result)
            })
          })
          return !!(result.auth_token || result.user_info)
        } catch (error) {
          console.error(':', error)
          return false
        }
      }
      
      return false
    },
    
    /**
     *  false
     * 
     * @returns {Promise<boolean>} 
     */
    async $requireAuth() {
      const isAuthenticated = await this.checkAuthStatus()
      
      if (!isAuthenticated) {
        this.$notify.warning({
          title: '',
          message: '',
          duration: 3000
        })
        return false
      }
      
      return true
    },
    
    /**
     * 
     * : @click="$auth(handleEquipPrice, role)"
     * @param {Function} fn 
     * @param {...any} args 
     * @returns {Promise<any>} 
     */
    async $auth(fn, ...args) {
      const isAuthenticated = await this.checkAuthStatus()
      
      if (!isAuthenticated) {
        this.$notify.warning({
          title: '',
          message: '',
          duration: 3000
        })
        return false
      }
      
      return fn.apply(this, args)
    }
  }
}

